<template>
    <Table :columns="columns" :data="data"></Table>
</template>
<script>
    import axios from "axios";
    export default {
         data () {
            return {
                columns: [
                    {
                        title: '',
                        key: 'order_sn'
                    },
                    {
                        title: '',
                        key: 'business_name'
                    },
                    {
                        title: '',
                        key: 'duration'
                    },
                    {
                        title: '',
                        key: 'machine_sn'
                    },
                    {
                        title: '',
                        key: 'order_status'
                    },
                    {
                        title: '',
                        key: 'order_type'
                    },
                    {
                        title: '',
                        key: 'pay_price'
                    },
                    {
                         title: '',
                         key: 'pay_time'
                    },
                    {
                        title: '',
                        key: 'pay_type'
                    },
                    {
                        title: '',
                        key: 'payable_money'
                    }
                ],
                data: [
                ]
            }
        },
        mounted() {
            axios.get("http://"+location.hostname+"/home/customer/orderList").then(res => {
                if(res.data.code==1) {
                    this.data = res.data.data;
                } else {
                    console.error(res.data.msg);
                }
            });
        }
    }
</script>